.grid {
  @include flex(flex-start, normal, row, wrap);
  &__left,
  &__right {
    width: 50%;
    @include flex(flex-start, normal, column);
    @include sm {
      width: 100%;
      padding-left: 0;
      padding-right: 0; } }
  &__left {
    padding-right: 15px;
    @include sm {
      margin-bottom: 15px; } }
  &__left-center {
    align-items: center;
    @include sm {
      align-items: normal; } }
  &__right {
    padding-left: 15px; } }
