.hero {
  padding-top: 20px;
  // overflow: hidden
  position: relative;
  z-index: 1;
  height: 90vh;
  @include flex(flex-start, normal, column);
  .container {
    width: 100%; }
  &__breadcrumbs {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__img {
    text-align: center;
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%; }

  &__img-in {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    padding-top: 35%;
    @include vcenter;
    width: 100%;
    @include xl {
      padding-top: 40%; }
    @include lg {
      padding-top: 50%; }
    @include md {
      padding-top: 55%; }
    @include sm {
      padding-top: 70%; }
    @include xs {
      padding-top: 85%; }
    &::before, &::after {
      content: '';
      border-right: 1px solid  $gray-lighten;
      bottom: 100%;
      z-index: 0;
      height: 50vh;
      position: absolute; }
    &::before {
      left: 37vw;
      @include sm {
        left: 30vw; } }
    &::after {
      right: 37vw;
      @include sm {
        right: 30vw; } } }
  &__content {
    @include flex(center, center);
    position: relative;
    flex-grow: 1;
    max-height: calc(100% - 44px); }
  &__title {
    @include css-lock(24, 130, 320, 1399, font-size);
    font-weight: 500;
    width: 100%;
    white-space: nowrap;
    color: $gray-lighten;
    position: fixed;
    z-index: 0;
    left: 0;
    text-align: center;
    // transform: translate(-50%, 0%)
    top: calc(50vh + #{$header-height});
    @include md {
      top: calc(50vh + #{$header-height-md}); }
    @include sm {
      top: calc(50vh + #{$header-height-sm}); }
    @include xs {
      top: calc(50vh + #{$header-height-xs}); } } }

.hero-link {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100px;
  font-weight: 500;
  color: $black;
  text-decoration: underline;
  text-transform: uppercase;
  z-index: 1;
  @include flex(center, center);
  @include sm {
    width: 80px; }
  @include xs {
    width: 60px; }
  a {
    @include rotate(-90);
    display: inline-block;
    white-space: nowrap;
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  &--prev {
    left: 0;
    // +after
    //// position: absolute
    //// border-right: 1px solid  $gray-lighten
    //// right: 0
    //// top: -100vh
 }    //// bottom: -500vh
  &--next {
    right: 0; } }
