.fadeIn {
  animation-name: fadeIn; }
.fadeInUp {
  animation-name: fadeInUp; }


@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate(0, 50px); }
  100% {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes moveDown {
  0% {
    transform: translate(0, -100%); }
  100% {
    transform: translate(0, 0); } }
